:root {
  --theme-col: #0786A7;
}

.hovBlu:hover {
  color: var(--theme-col) !important;
}

.profile-pic {
  height: 4vmin;
  margin-right: 10px;
  border-radius: 50px;
}

.jumbotron {
  background-color: transparent !important;
  text-align: left !important;
}

.pages {
  text-align: left !important;
}

.drpdwn {
  font-size: 200% !important;
  /*background-color: white !important;*/
  border: none !important;
  padding: 20px !important;
  /*outline: none !important;*/
}

.drpdwn:hover {
  color: var(--theme-col) !important;
  /*border: none !important;*/
  /*outline: none !important;*/
}

.slide_img {
  margin: auto;
  width: 100%;
  height: 300px;
  object-fit: contain;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em auto;
  padding: 0;
  width: 70%;
  background-color: var(--theme-col);
}

.item:hover {
  text-decoration: underline var(--theme-col);
}

.ctrl {
  /*background-color: black !important;*/
  filter: invert(14%);
}

footer {
  margin-top: 20px;
  margin-bottom: 20px;
  border-top: #444444;
  text-align: center;
}

footer a {
  color: black;
  font-size: 30px;
  margin-right: 10px;
  margin-left: 10px;
}
